import { IDsMessageService } from "./ds-msg.interface";
import { dsMessage, dsMessageDetail } from "./ds-msg-message.model";
import { MessageTypes } from "./ds-msg-msgTypes.enumeration";
import { IconTypes } from "./ds-msg-iconTypes.enumeration";

/**
 * @ngservice
 * @name Ess.Services:MessageService
 * @requires $rootScope - Angular root scope object used to monitor & handle routing changes.
 * @requires $timeout - Angular timeout service used to perform a function in a specified number of milliseconds.
 * 
 * @description
 * Service used to set the global message object displayed at the top of a page in the ESS application.  
 * This service also displays 'loading' and 'error' messages as needed during route changes.
 */
export class DsMsgService implements IDsMessageService {
    static readonly SERVICE_NAME = 'DsMsg';
    static readonly $inject = ['$rootScope', '$timeout'];

    displayRouteChangeSuccessMessageFlag = true;
    onRouteChangeRequestMessageData      = null;
    temporaryMessageData                 = null;
    timeoutMessageReference              = null;
    routeKey                             = ''; //empty string is the 'key' for the default message directive

    /**
     * The current message.
     */
    message: dsMessage = {
        type: "",
        icon: "",
        text: "",
        textColor: null,
        details: []
    };

    messageTypes = MessageTypes;
    iconTypes = IconTypes;

    constructor(private $rootScope: ng.IRootScopeService, private $timeout: ng.ITimeoutService) {

    }

    static get() {
        return (root, timeout) => new DsMsgService(root, timeout);
    }

    registerStateChangeListeners() {
            
        this.$rootScope.$on("$stateChangeStart", (event, next, current) => {
            this.clearRouteKey();

            if(this.isTimeoutMessageActive())
                this.clearTemporaryMessage();
                
            this.loading(true);
        });

        this.$rootScope.$on("$stateChangeSuccess", (event, current, previous) => {
            if (this.displayRouteChangeSuccessMessageFlag) {

                if (!!this.onRouteChangeRequestMessageData) {
                    //console.log('%cROUTE CHANGE SUCCESS', 'color:green; font-size:16pt;');
                    this.displayOnRouteChangeRequestMessage();
                    this.onRouteChangeRequestMessageData = null;
                } else {
                    this.clearMessage();
                }
            } else
                this.displayRouteChangeSuccessMessageFlag = true;
        });

        this.$rootScope.$on("$stateChangeError", (event, toState, toParams, fromState, fromParams, rejection) => {
            this.setXhrError(rejection);
        });

    }

    setRouteKey(routeKey: string) {
        this.routeKey = routeKey;
        return this;
    }

    clearRouteKey() {
        this.routeKey = '';
    }

    /**
    * @ngmethod
    * @name #overrideMessageText
    * @methodOf ds.legacy:MessageService
    * @param messageText - Text to update the main message to.
    * 
    * @description
    * Convenience method used to update only the message text of the current message settings.
    */
    overrideMessageText(messageText:string) {
        this.message.text = messageText;
        this.broadcastMessageUpdate();
    }

    /**
    * @ngmethod
    * @name #checkForErrors
    * @methodOf ds.legacy:MessageService
    * @param data - data returned from either a successful or erroneous xhr request 
    * @param successHandler(data) - function called if the data parameter does NOT contain errors, passing in the
    *        original data object as a parameter.
    * @param errorHandler(data) - function called if the data parameter DOES contain errors, passing in the
    *        original data object as a parameter.
    * 
    * @description
    * Checks the given data object for errors. If errors are found, display the error message and 
    * call the ErrorHandler; otherwise, call the SuccessHandler.
    */
    checkForErrors(data, successHandler:(...args)=>any, errorHandler?:(...args)=>any) {

        if (!data.errors) {
            successHandler(data);
        } else {
            this.setXhrError(data);
            this.displayRouteChangeSuccessMessageFlag = false;
            if (errorHandler)
                errorHandler(data);
        }

    }

    /**
    * @function
    * @private
    *
    * @description
    * Setup the message service to display a message after a successful route change.
    * See service comments for more information.
    */
    setOnRouteChangeSuccessMessage(msg:string, messageType?:MessageTypes, messageDisplayDuration?:number) {
        this.onRouteChangeRequestMessageData = {
            msg: msg,
            messageType: messageType || MessageTypes.success,
            messageDisplayDuration: messageDisplayDuration || 6000
        };
    }

    /**
    * @function
    * @private
    *
    * @description
    * This is similar to the route change request message except this is shown immediately.
    * It will dissappear after the specified millisconds (if provided)
    */
    setTemporarySuccessMessage(msg:string, messageDisplayDuration?:number) {
        this.setOnRouteChangeSuccessMessage(msg, MessageTypes.success, messageDisplayDuration);
        this.displayOnRouteChangeRequestMessage();
    }

    /**
    * @function
    * @private
    *
    * @description
    * This is similar to the route change request message except this is shown immediately.
    * It takes a message type as input.
    * It will dissappear after the specified millisconds (if provided)
    */
    setTemporaryMessage(msg: string, msgType?: MessageTypes, messageDisplayDuration?: number, msgColor? : string) {

        if (!msgType)
            msgType = MessageTypes.success;

        this.message.textColor = msgColor;

        this.onRouteChangeRequestMessageData = {
            msg: msg,
            messageType: msgType,
            messageDisplayDuration: messageDisplayDuration || 6000
        };

        this.displayOnRouteChangeRequestMessage();
    }
    
    showHttpErrorMsg(errorResponse: any, operation?: string){
        const errorMsg = errorResponse.error.errors != null && errorResponse.error.errors.length
            ? errorResponse.error.errors[0].msg
            : errorResponse.message;

        if (operation) {
            console.error(`--- ${operation} failed! ---`);
            console.error(errorMsg);
            
            // TODO: WE SHOULD CONSIDER DOING SOME LOGGING HERE?
        }
        
        this.setTemporaryMessage(`${errorMsg}`, MessageTypes.error, 6000);
    }

    /**
    * @ngmethod
    * @name #cancelTimeoutMessageIfExists
    * 
    * @description
    * if the timeout reference isn't null then this will cancel it and set the reference to null.
    */
    cancelTimeoutMessageIfExists() {
        if (this.isTimeoutMessageActive())
            this.$timeout.cancel(this.timeoutMessageReference);

        this.timeoutMessageReference = null;
    }

    /**
    * @ngmethod
    * @name #isTimeoutMessageActive
    * 
    * @description
    * Indicates if a timeout message is currently being displayed.
    */
    isTimeoutMessageActive() {
        return !!this.timeoutMessageReference;
    }

    /**
    * @ngmethod
    * @name #displayOnRouteChangeRequestMessage
    * 
    * @description
    * Display the on route change success message and set it to clear after the specified time
    * in the message's metadata stored the local object.
    */
    displayOnRouteChangeRequestMessage() {
        this.cancelTimeoutMessageIfExists();

        this.setMessage(
            this.onRouteChangeRequestMessageData.msg,
            this.onRouteChangeRequestMessageData.messageType);

        this.timeoutMessageReference = this.$timeout(
            () => {
                //console.log('%cCLEARING TIMER FIRED', 'color:RED; font-size:16pt;');
                this.clearMessage();
                this.clearTemporaryMessage();
            },
            this.onRouteChangeRequestMessageData.messageDisplayDuration
        );
    }

    /**
    * @function
    * @private
    *
    * @description
    * Sets the current message object to the provided message settings
    */
    setMessage(msg: string, messageType: MessageTypes | "", details?:dsMessageDetail[]) {
        //console.log('SET MESSAGE %s - %s - %O', msg, messageType, details);
        //console.trace();
        this.cancelTimeoutMessageIfExists();
        //svc.message.routeKey = svc.routeKey;
        this.message.text = msg;
        this.message.type = messageType;

        if (details)
            this.message.details = details;
        else
            this.message.details = [];

        //lowfix: jay: for now I'm sending in the details as null until we can determine 100% how we want to show the messages.
        //svc.message.details = [];

        this.broadcastMessageUpdate();
        return this;
    }

    
    setMessageWithIcon(msg:string, messageType:MessageTypes | "", iconType:IconTypes, details:dsMessageDetail[]) {
        //console.log('SET MESSAGE %s - %s - %O', msg, messageType, details);
        //console.trace();
        this.cancelTimeoutMessageIfExists();
        //svc.message.routeKey = svc.routeKey;
        this.message.text = msg;
        this.message.type = messageType;
        this.message.icon = iconType;

        if (details)
            this.message.details = details;
        else
            this.message.details = [];

        //lowfix: jay: for now I'm sending in the details as null until we can determine 100% how we want to show the messages.
        //svc.message.details = [];

        this.broadcastMessageUpdate();
        return this;
    }

    /**
    * @ngmethod
    * @name #getMessage
    * @methodOf ds.legacy:MessageService
    * 
    * @description
    * Returns the current value of the message object
    */
    getMessage() {
        return this.message;
    }

    /**
    * @function
    * @private
    *
    * @description
    * Raises an angular event indicating the message has been updated that can be 
    * watched by other areas of the application.
    */
    broadcastMessageUpdate() {
        this.$rootScope.$broadcast('notificationMessageUpdated', this.routeKey, this.message);
    }

    /**
    * @function
    * @private
    *
    * @description
    * Clears the current message.
    */
    clearMessage() {
        //console.log('CLEAR MESSAGE');
        //console.trace();
        this.cancelTimeoutMessageIfExists();
        this.setMessage("", "", []);
    }

    /**
   * @function
   * @private
   *
   * @description
   * Clears the current temporary message.
   */
    clearTemporaryMessage() {
        this.cancelTimeoutMessageIfExists();
        this.onRouteChangeRequestMessageData = null;
    }

    /**
    * @function
    * @private
    *
    * @description
    * Sets the current message object to an error message containing the specified XHR errors
    * obsolete: jay: replace these calls with newer error handlers: showWebApiException(data) 
    */
    setXhrError = (xhrErrorData) => {
        this.cancelTimeoutMessageIfExists();
        this.setMessage(xhrErrorData.message, MessageTypes.error, xhrErrorData.errors);
    }

    /**
    * @function
    * @private
    *
    * @description
    * Take in an object that contains an exception thrown by the web api.
    */
    showWebApiException = (data) => {
        this.cancelTimeoutMessageIfExists();
        if(data && !data.data){
            if(data.message)
                this.setMessage(data.message, MessageTypes.error, data.errors);
            else
                this.setMessage("An unknown error has occurred.",MessageTypes.error)
        }else{
            this.setMessage(data.data.message, MessageTypes.error, data.data.errors);
        }
    }

    /**
    * @function
    * @private
    *
    * @description
    * Shows a single IOpResult error message. IOpResult is the vehicle for our server side messages.
    */
    showSingleOpErrorMsg(msg) {
        this.cancelTimeoutMessageIfExists();
        this.setMessage(msg.message, MessageTypes.error, null);
    }

    /**
    * @function
    * @private
    *
    * @description
    * Shows an error message. 
    */
    showErrorMsg(msg) {
        this.cancelTimeoutMessageIfExists();
        this.setMessage(msg, MessageTypes.error, null);
    }

    /**
    * @function
    * @private
    *
    * @description
    * Sets notification to a standardized 'Loading...' message
    */
    loading(isLoading?:boolean) {
        this.cancelTimeoutMessageIfExists();
        if (isLoading)
            this.setMessage("Loading...", MessageTypes.warning);
        else
            this.clearMessage();
    }

    /**
    * @function
    * @private
    *
    * @description
    * Sets notification to a standardized 'Sending...' message
    */
    sending(isSending?:boolean) {
        this.cancelTimeoutMessageIfExists();
        if (isSending)
            this.setMessage("Sending...", MessageTypes.warning);
        else
            this.clearMessage();
    }
}